    justify-content: space-between;
    align-content: space-between;
    .item{
      width: calc(33.3% - 12px);
      height: calc(50% - 10px);
      .nav{
        height: 38px;
        line-height: 38px;
        font-size: 18px;
        color: #4389F9;
        font-weight: 700;
        padding: 0 12px;
        > img, span {
          vertical-align: middle;
        }
      }
      .charts{
        height: calc(100% - 38px);
      }
    }
  }
  .supervisionPlanExecutionCharts {
    .item{
      background-image: linear-gradient(0deg, rgba(0, 122, 255, 0.5) 0%, rgba(0, 66, 128, 0.1) 11%, rgba(0, 31, 102, 0.1) 88%, rgba(0, 126, 255, 0.7) 100%);
