<template>
    <div class="barCharts">
        <div id="barCharts" ref="barCharts"></div>
    </div>
</template>

<script>
  import * as echarts from 'echarts';
  import bus from '@/utils/bus';
  //这是绘制图表的参数定义
  let optionTemp =
    {
      tooltip: {
        trigger: 'axis',
        backgroundColor: 'rgba(42, 92, 176, .6)',
        borderColor: "rgba(0, 255, 239, 1.0)",
        textStyle: {
          color: '#00FFEF'
        },
        axisPointer: {
          type: 'cross',
          crossStyle: {
            color: '#999'
          }
        }
      },
      grid: {
        left: '4%',
        top: '25%',
        right: '4%',
        bottom: '20',
        containLabel: true
      },
      legend: {
        // show:false,
        data: [
          // 'legend1', 'legend2'
          {icon:'circle',name:'legend1'},
          {icon:'rect',name:'legend2'},
        ],
        // x:'right',      //可设定图例在左、右、居中
        // y:'top',     //可设定图例在上、下、居中
        top: '-2px',
        right: '25%',
        padding:[0,50,0,0],   //可设定图例[距上方距离，距右方距离，距下方距离，距左方距离
        textStyle: {color: '#fff'},
      },
      xAxis: [
        {
          type: 'category',
          data: [0,0],
          axisPointer: {
            type: 'shadow'
          },
          axisTick: { //x轴刻度线
            show: false,
          },
          axisLine: { //x轴
            show: false,
            // lineStyle:{
            //   color:'#00FF00',
            // }
          },
          axisLabel: { //设置x轴的字
            show: true,
            interval:0,//使x轴横坐标全部显示
            // rotate:50,//旋转
            textStyle: {//x轴字体样式
              color: "#fff",
              margin: 15,
              fontWeight: 'bold',
            }
          },
        }
      ],
      yAxis: [
        {
          type: 'value',
          name: '单位：个',
          interval: 20,
          max:null,
          nameTextStyle:{
            color:"#66B9FF", 
        },
          axisLabel: { //设置y轴的字
            // show: true,
            // formatter: '{value} ml',
            color:'#fff',
            interval: 0,//使y轴横坐标全部显示
            textStyle: {//y轴字体样式
              color: "#fff",
              margin: 15,
            }
          },
          splitLine: {//网格线样式
            show: true,
            lineStyle:{
              color: '#66B9FF',
              width: 1,
              type: 'solid'
            }
          },
          axisTick: { //y轴刻度线
            show: false,
          },
          axisLine: { //y轴
            show: false,
          },
        },
      ],
      series: [
        {
          name: 'legend1',
          type: 'bar',
          data: [20,10,30,60,70],
          itemStyle: {
            normal: {
              color: new echarts.graphic.LinearGradient(0, 1, 0, 0, [{
                offset: 0,
                color: "#1268f3" // 0% 处的颜色
              }, {
                offset: 0.6,
                color: "#08a4fa" // 60% 处的颜色
              }, {
                offset: 1,
                color: "#01ccfe" // 100% 处的颜色
              }], false)
            }
          },
        },
        {
          name: 'legend2',
          type: 'bar',
          yAxisIndex: 1,
          data: [20,10,30,60,70]
        },
      ]
    }


  export default {
    name: "barCharts",
    props: {
      yStyle:{
        type:Number,
        default:()=>{
          return null
        }
      },
      legend:{
         type:Object,
        default:()=>{
          return {}
        }
      },
      chartMax:{
        type:Number,
        default:()=>{
          return null
        }
      },
      dataZoom:{
        type:Array,
        default:()=>{
          return []
        }
      },
      AddYaxis: {
        type:Object,
        default:()=>{
          return {}
        }
      },
      index:{
        type:Number,
        default:()=>{
          return null
        }
      },
      tooltipFormatter:{
        type:Boolean,
        default:()=>{
          return false
        }
      },
      yAxisData:{ //针对多个Y坐标维度
        type:Array,
        default:()=>{
          return []
        }
      },
      xData:{
        /*** * xData:['一月',二月]*/
        type:Array,
        default:()=>{
          return []
        }
      },
      seriesData: {
        /*** * seriesData:[{
          name: 'legend1',
          type: 'bar',
          data: [20,10,30,60,70],
          itemStyle: {
            normal: {
              color: new echarts.graphic.LinearGradient(0, 1, 0, 0, [{
                offset: 0,
                color: "#1268f3" // 0% 处的颜色
              }, {
                offset: 0.6,
                color: "#08a4fa" // 60% 处的颜色
              }, {
                offset: 1,
                color: "#01ccfe" // 100% 处的颜色
              }], false)
            }
          },
        },]*/
        type: Array,
        default: () => {
          return []
        }
      },
      //  决定x轴和y轴显示(单位)
      _zb: {
        /** :['单位','']*/
        type: Array,
        default: () => {
          return ['单位', '']
        },

        //echarts 样式
        legendStyle:{
          type:Object,
          default:()=>{
            return {color: '#333333'}
          }
        },
        xStyle:{
          type:Object,
          default:()=>{
            return {
              color: "#333333",
              margin: 15,
              fontWeight: 'bold',
            }
          }
        },
        yStyle:{
          type:Object,
          default:()=>{
            return {
              color: "#333333",
              margin: 15,
            }
          }
        },
      },
      xNameClass:{ //x周单位是否全部显示
        type:Object,
        default:()=>{
          return {
            noShows: false,
            nums: 5,
          }
        }
      },
    },
    watch: {
      '$root.themeHomeChange'() {
        this.init();
      }
    },
    data() {
      return {
        time: null,
        option: {},
        mychart:null,
      }


    },
    mounted() {
      this.time = setInterval(()=>{
        this.mychart.resize()
      },300);
      this.init();
    },
    methods: {
      setTheme(){
        //样式区分
        if (this.$root.themeHomeChange === '1'){  //深色背景
          this.option.legend.textStyle = {color: '#333333'}
          this.option.xAxis[0].axisLabel.textStyle = {
            color: "#333333",
            margin: 15,
            fontWeight: 'bold',
          }
          this.option.yAxis[0].axisLabel.textStyle = {
            color: "#333333",
            margin: 15,
            fontWeight: 'bold',
          }
          this.option.yAxis.forEach((item,i) => {
            item.splitLine = {//网格线样式
              show: true,
              lineStyle:{
                color: '#E6E6E6',
                width: 1,
                type: 'solid'
              }
            }
            if (i!=0){
              item.splitLine.show = false
            }

          });
        }else if(this.$root.themeHomeChange==='2'){ //浅色背景
          this.option.legend.textStyle = {color: '#ffffff'}
          this.option.xAxis[0].axisLabel.textStyle = {
            color: "#ffffff",
            margin: 15,
            fontWeight: 'bold',
          }
          this.option.yAxis[0].axisLabel.textStyle = {
            color: "#66B9FF",
            margin: 15,
            fontWeight: 'bold',
          }
          this.option.yAxis.forEach((item,i) => {
              item.splitLine = {//网格线样式
                show: true,
                lineStyle:{
                  color: '#66B9FF',
                  width: 1,
                  type: 'solid'
                }
              }
            if (i!=0){
              item.splitLine.show = false
            }
          });
        }
      },
      dataSet(){
        // 保证源配置不被污染
        this.option = JSON.parse(JSON.stringify(optionTemp));
        
        this.option.yAxis[0].name = this._zb[0]
        this.option.xAxis[0].name = this._zb[1]
        this.option.xAxis[0].data = this.xData
        this.option.yAxis[0].interval = this.yStyle
        this.option.legend.show = this.legend.legend
        if (this.legend.padding2){
          this.option.legend.padding = this.legend.padding2
        }
        if (this.AddYaxis.type){
          this.option.yAxis.push(this.AddYaxis);
        }
        this.option.yAxis[0].max = this.chartMax
        this.option.dataZoom = this.dataZoom
        if (this.seriesData.length>0){
          this.option.legend.data = []
          this.seriesData.forEach(item=>{
            this.option.legend.data.push(item.name)
          })
          //格式化tooltip
          if (this.tooltipFormatter){
            this.option.tooltip.formatter =  (v) => {
              v.filter(p=>{
                if (p.seriesName==='整改率'){
                  return p.value = p.value+'%'
                }
              })
              let str = (v instanceof Array)? v[0].name:''
              v.forEach(v=>{
                str += '<br>'+ v.marker +v.seriesName+': '+ v.value
              })
              return str
            }
          }
          this.option.series = this.seriesData
        }
        if(this.seriesData[0].data.length > 7){
          this.option.xAxis[0].axisLabel.formatter = function(params){
            var val = ''
            if(params.length > 4){
              val = params.substr(0,3) + '...';
              return val;
            }else{
              return params;
            }
          }
        }
        if(this.xNameClass.noShows){
          if (this.seriesData[0].data.length > 5&&this.seriesData[0].data.length <7 ){
            let _this = this
            this.option.xAxis[0].axisLabel.formatter = function(params){
              var val = ''
              if(params.length >= _this.xNameClass.nums){
                val = params.substr(0,_this.xNameClass.nums) + '...';
                return val;
              }else{
                return params;
              }
            }
          }else if (this.seriesData[0].data.length >=7 ){
            let _this = this
            this.option.xAxis[0].axisLabel.formatter = function(params){
              var val = ''
              if(params.length >= _this.xNameClass.nums){
                val = params.substr(0,_this.xNameClass.nums) + '...';
                return val;
              }else{
                return params;
              }
            }
            this.option.xAxis[0].axisLabel.rotate = 30
          }
        }
        //针对多个Y坐标维度
        if (this.yAxisData.length>0){
          this.option.yAxis = this.yAxisData
        }

        //样式
        this.setTheme()
      },
      init() {
        this.dataSet()
        this.mychart = echarts.init(this.$refs.barCharts, );
        //将会绘制图表的参数加到图表中

        this.mychart.setOption(this.option,true);
        let $this = this;
        this.mychart.on('click', function (params) {
          $this.$emit('getData',params,$this.index)
          // window.console.log(params);
        });
        window.addEventListener("resize", ()=>{
          this.mychart.resize()
        });

      },
    },
    beforeDestroy() {
      /* 页面组件销毁的时候，别忘了移除绑定的监听resize事件，否则的话，多渲染几次
      容易导致内存泄漏和额外CPU或GPU占用哦*/
      bus.$off('isCollapse');
      clearInterval(this.time);
      window.removeEventListener("resize", () => {
        this.myChart.resize();
      });
    },

  }
</script>

<style scoped lang="scss">
    .barCharts {
        height: 100%;
        #barCharts {
            height: 100%;
            /*width: 97%;*/
            /*height: 99%;*/
        }
    }
</style>